.order-add-cart-block {
    width: 40%;
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: #262328;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    margin-top: 1%;
}
.sbp-button{
    width: 100%;
    text-align: center;
    margin-top: 5%;
    margin-bottom: 5%;
}
.sbp-button a{
    width: 100%;
    display: block;
}
.sbp-button a>img{
    width: 90%;
}

.add-cart-block{
    width: 100%;
}

label {
    display: block;
    margin-bottom: 5%;
    color: #E1FF00;
    font-size: 20px;
    margin-left: 5%;
}
.card-number-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card-number-container::after {
    content: attr(data-card-type);
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    padding: 0 10px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #000;
}

input[type="text"], input[type="number"] {
    width: 90%;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: #211F22;
    color: white;
    font-size: 20px;
}

input[type=text]:focus, input[type="number"]:focus{
    outline: 2px solid #E1FF00;
}

.cvv-container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 20px;
}
.cvv-container input {
    width: 42%;

}
input[type="submit"] {
    width: 90%;
    color: #fff;
    border: none;
    padding: 15px 20px;
    border-radius: 8px;
    background: #8B53E8;
    cursor: pointer;
    font-size: 20px;
    transition: background-color 500ms ease-in-out;
}
input[type="submit"]:hover {
    background-color: #522F8B;
}
.submit-button{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 5%;
}
.accept-text{
    width: 100%;
    text-align: center;
}
.accept-text p{
    color: rgba(255, 255, 255, 0.40);
    font-size: 18px;
}

